import { Injectable } from '@angular/core';
import { API } from './api';

@Injectable()
export class ImageService {
    constructor(private api: API) {}

    getImage(id) {
        return this.api.get('/images/' + id);
    }

    getAllImages() {
        return this.api.get('/images');
    }

    createImage(data) {
        return this.api.post('/images', data);
    }

    updateImage(id, data) {
        return this.api.put('/images/' + id, data);
    }

    deleteImage(id) {
        return this.api.delete('/images/' + id);
    }

    getAlbums() {
        return this.api.get('/albums');
    }

    createAlbum(data) {
        return this.api.post('/albums', data);
    }

    getAlbum(albumId) {
        return this.api.get('/albums/' + albumId);
    }
}
